import React from 'react';
import { usePagePath } from '../../hooks';
import { PatternsReactRoute, PatternsReactRouter } from '@wix/patterns/router';
import { AutoPatternsPage } from './AutoPatternsPage';
import type { RouteType } from '@wix/patterns/dist/types/components/Router/types';
import { Navigate } from 'react-router-dom';
import { AppConfig } from '../../types';

const pageTypeToRouteType: Record<string, RouteType> = {
  collectionPage: 'collection',
  entityPage: 'editEntity',
};

export const AutoPatternsRoutes = ({ config }: { config: AppConfig }) => {
  const { getPagePath } = usePagePath(config);
  const mainPage = config.pages.find((page) => page.appMainPage);

  const routes = config.pages
    .map((page) => {
      const routePath = getPagePath(page.id);

      if (!routePath) {
        return null;
      }

      return (
        <PatternsReactRoute
          key={page.id}
          type={pageTypeToRouteType[page.type] ?? 'other'}
          path={routePath}
          element={<AutoPatternsPage pageConfig={page} />}
        />
      );
    })
    .filter(Boolean);

  if (mainPage) {
    const mainPagePath = getPagePath(mainPage.id);

    if (mainPagePath) {
      routes.push(
        <PatternsReactRoute
          key="main-redirect"
          type="other"
          path="/"
          element={<Navigate to={mainPagePath} />}
        />,
      );
    }
  }

  return <PatternsReactRouter>{routes}</PatternsReactRouter>;
};
